.form-control {
    margin-bottom: 1rem;
    margin-top: 20px;
  }
  
  .form-control input,
  .form-control label {
    display: block;
  }
  
  .form-control label {
    font-weight: bold;
    margin-bottom: 0.5rem;
  }
  
  .form-control input,
  .form-control select {
    font: inherit;
    border-radius: 4px;
    border: 1px solid #ccc;
    width: 20rem;
    max-width: 100%;
  }
  
  .form-control input:focus {
    outline: none;
    border-color: #240370;
    background-color: #e0d4fd;
  }
  
  .control-group {
    display: flex;
    column-gap: 1rem;
    flex-wrap: wrap;
  }
  
  .control-group .form-control {
    min-width: 15rem;
    flex: 1;
  }
  
  
  .form-actions {
    text-align: right;
  }
  
  
  .invalid input {
    border: 1px solid #b40e0e;
    background-color: #fddddd;
  }
  
  .invalid input:focus {
    border-color: #ff8800;
    background-color: #fbe8d2;
  }
  
  .error-text {
    color: #b40e0e;
  }
